//
// To make future updates easier consider overriding the global variables from _variables.bootstrap.scss and _variables.custom.scss for current demo in this file.
// Note that this file is included first and variables defined in _variables.bootstrap.scss and _variables.custom.scss
// are not accessible in this file but you can override any global variable as shown below:
//

// Bootstrap grey colors
$gray-100: 											#F9F9F9;
$gray-200: 											#F4F4F4;
$gray-300: 											#E1E3EA;
$gray-400: 											#B5B5C3;
$gray-500: 											#A1A5B7;
$gray-600: 											#7E8299;
$gray-700: 											#5E6278;
$gray-800: 											#3F4254;
$gray-900: 											#181C32;

// Theme colors
$primary:       									#034EA2;
$primary-active:    								#2884Ef;
$primary-light:    									rgba(3, 78, 165, 0.15);
$primary-light-dark:    							#212E48;
$primary-inverse:  									#FFFFFF;

// Secondary colors
$secondary: 					                    rgba(3, 78, 165, 0.15);
$secondary-active: 				                    rgba(3, 78, 165, 0.25);
// $secondary-light: 				                $gray-100;
// $secondary-inverse:				                $gray-800;

// Light colors
$light: 											$gray-100;

// Dark colors
$dark: 												$gray-900;

// Success
$success:       									#1EBB79;
$success-active:    								#47be7d;
$success-light:    								    #e8fff3;
$success-light-dark:							    #1c3238;
$success-inverse:  									#FFFFFF;

// Info
$info:       									    #7239ea;
$info-active:    									#5014d0;
$info-light:    									#f8f5ff;
$info-light-dark:    								#2f264f;
$info-inverse:  									#FFFFFF;

// Danger
$danger:       									    #FF5630;
$danger-active:    									#d9214e;
$danger-light:    									rgba($danger, 15%);
$danger-light-dark:    								#3a2434;
$danger-inverse:  									#FFFFFF;

// Warning
$warning:       									#FFA600;
$warning-active:    								#f1bc00;
$warning-light:    									#fff8dd;
$warning-light-dark:    							#392f28;
$warning-inverse:  									#FFFFFF;

// Purple
$purple:       										#8E24AA;
$purple-active:    									#5014d0;
$purple-light:    									rgba($purple, 15%);
$purple-light-dark:    								#2f264f;
$purple-inverse:  									#FFFFFF;

// Dark Green
$dark-green:       									#009688;
$dark-green-active:    								#47be7d;
$dark-green-light:    								rgba($dark-green, 15%);
$dark-green-light-dark:    							#1c3238;
$dark-green-inverse:  								#FFFFFF;

// SOS
$sos:       										#E91E63;
$sos-active:    									#d9214e;
$sos-light:    										rgba($sos, 15%);
$sos-light-dark:    								#3a2434;
$sos-inverse:  										#FFFFFF;


// Contextual colors
$theme-colors: (
	"light": $light,
	"primary": $primary,
	"secondary": $secondary,
	"success": $success,
	"info": $info,
	"warning": $warning,
	"danger": $danger,
	"dark": $dark,
	"purple": $purple,
	"dark-green": $dark-green,
	"sos": $sos,
);
// Opacity
$opacity-values: (
	0: 0,
	5: .05,
	10: .1,
	15: .15,
	20: .20,
	25: .25,
	50: .5,
	75: .75,
	80: .8,
	100: 1,
 );
 
// Body
$body-color:                						$gray-900;

// Muted
$text-muted:                                        #7E7E7E;

// Card
$card-box-shadow: 									none; 
$card-border-enabled: 								true;

// Border Radiues
$border-radius-sm:            						.35rem;
$border-radius:               						5px;
$border-radius-lg:            						.85rem;
$border-radius-xl:                                  1.25rem;

// Progress bars
$progress-bg:                                       #EBEBEB;

// Menu link
$menu-accordion-indention: 0;
$menu-link-bg-color-active:                         $secondary;

// Accordion
$accordion-icon-width:                          	.5rem;
$accordion-icon-color:                          	$body-color;
$accordion-icon-active-color:                   	$primary;
$accordion-button-icon:                         	url("data:image/svg+xml,<svg viewBox='0 0 6 6' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M0.0740446 2.82328L2.91993 0.172033C3.34883 -0.227536 4 0.113425 4 0.737569L4 5.26243C4 5.88658 3.34883 6.22753 2.91993 5.82797L0.0740447 3.17673C-0.0246818 3.08476 -0.0246819 2.91524 0.0740446 2.82328Z' fill='#{$accordion-icon-color}'/></svg>");
$accordion-button-active-icon:                  	url("data:image/svg+xml,<svg viewBox='0 0 6 6' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M3.17672 0.0740445L5.82797 2.91993C6.22754 3.34883 5.88657 4 5.26243 4L0.73757 4C0.113423 4 -0.227534 3.34883 0.172033 2.91993L2.82327 0.0740445C2.91524 -0.024682 3.08476 -0.024682 3.17672 0.0740445Z' fill='#{$accordion-icon-active-color}'/></svg>");


